// 
const blockType = [ 
                    [[0, 0], [1, 0], [2, 0], [3, 0]],
                    [[0, 0], [0, 1], [1, 0], [1, 1]],
                    [[0, 1], [1, 0], [1, 1], [2, 1]],
                    [[0, 0], [0, 1], [1, 1], [2, 1]],
                    [[0, 1], [1, 1], [2, 0], [2, 1]],
                    [[0, 0], [1, 0], [1, 1], [2, 1]],
                    [[0, 1], [1, 0], [1, 1], [2, 0]],
]

// 
const clearPoints = [100, 300, 500, 800]

// 
const speeds = [800, 650, 500, 400, 300, 200]

// 
const delays = [50, 60, 70, 80, 90, 100]

// 
const linesForEachLevel = 20

// 
const blockScoreIncrement = 2

// 
const generateBlockProb = 0.6

// 
const maxStartLines = 10

// 
//     keyCode 
//     sel  HTML  id 
const keySettings = {
    rotate: {
        keyCode: 38,            // 
        sel: '#rotate_btn',
    },
    speedUp: {
        keyCode: 40,            // 
        sel: '#down_btn',
    },
    left: {
        keyCode: 37,            // 
        sel: '#left_btn',
    },
    right: {
        keyCode: 39,            // 
        sel: '#right_btn',
    },
    drop: {
        keyCode: 32,            // 
        sel: '#drop_btn',
    },
    pause: {
        keyCode: 80,            // P
        sel: '#pause_btn',
    },
    reset: {
        keyCode: 82,            // R
        sel: '#reset_btn',
    },
}

// 
const btnDownState = 'pressed'
const btnUpState = ''

// 
const pauseState = 'paused'
const unpauseState = 'unpaused'

// 
const tikState = 'tik'
const tokState = 'tok'

const defaultColor = 'rgb(135, 147, 100)'    // 
const occupiedColor = 'rgb(0, 0, 0)'         // 
const indicateColor = 'rgb(180, 0, 0)'       // 

export {
    blockType,
    clearPoints,
    speeds,
    delays,
    linesForEachLevel,
    blockScoreIncrement,
    generateBlockProb,
    maxStartLines,
    keySettings,
    btnDownState,
    btnUpState,
    pauseState,
    unpauseState,
    tikState,
    tokState,
    defaultColor,
    occupiedColor,
    indicateColor,
}


