// 
export default class GameScene {
    constructor (game) {
        this.game = game
        this.elements = []
        this.setup()
    }

    static instance(game) {
        this.i = this.i || new this(game)
        this.i.init()
        return this.i
    }

    setup() {}

    init() {}

    //  draw
    draw() {
        for (var e of this.elements) {
            e.draw()
        }
    }

    update() {}

    pushElement(elt) {
        this.elements.push(elt)
    }
}



