import {
    generateBlockProb,
    defaultColor,
    occupiedColor,
    indicateColor,
} from '../const'

// 
export default class GameArea {
    constructor (game) {
        this.game = game
        this.board = []
        this.scoring = false
        this.process = null
        this.count = 0
        this.default()
    }

    static instance(game) {
        this.i = this.i || new this(game)
        return this.i
    }

    default() {
        // 
        this.row = 20
        // 
        this.column = 10
        this.defaultColor = defaultColor
        this.indicateColor = indicateColor
        // 
        this.fullLinesEchoSpeed = 15    // board 
        this.fullLinesEchoTimes = 3     //  board 
    }

    // boardboard
    // occupied
    init() {
        let row = this.row
        for (let i = 0; i < row; i++) {
            this.board[i] = []
            let line = this.board[i]
            for (let j = 0; j < this.column; j++) {
                line[j] = {
                    color: this.defaultColor,
                    occupied: false,
                }
            }
        }
    }

    // 
    setPresetLines(num) {
        this.generateRandomRows(num)
    }

    // 
    setLine(lineIndex, color, occupied) {
        let line = this.board[lineIndex]
        for (let i = 0; i < this.column; i++) {
            let block = line[i]
            block.color = color
            block.occupied = occupied
        }
    }

    // 
    generateRandomRows(num) {
        let lastIndex = this.row - num
        for (let i = this.row - 1; i >= lastIndex; i--) {
            let row = this.board[i]
            for (let j = this.column - 1; j >= 0; j--) {
                let block = row[j]
                let generateBlock = Math.random() <= generateBlockProb
                block.color = generateBlock ? occupiedColor : defaultColor
                block.occupied = generateBlock
            }

            if (this.lineIsFull(row)) {
                let index = Math.floor(Math.random() * 10)
                let block = row[index]
                block.color = defaultColor
                block.occupied = false
            }
        }
    }

    lineIsFull(line) {
        return line.map(b => {
            return b.occupied
        }).reduce((prev, cur) => {
            return prev && cur
        })
    }

    // 
    setUpScoreRule(func) {
        this.scoreFunc = function(rows) {
            func && func(rows)
        }
    }

    draw() {
        let game = this.game
        for (let i = 0; i < this.row; i++) {
            for (let j = 0; j < this.column; j++) {
                game.drawBlock(j, i, this.board[i][j].color)
            }
        }
    }

    update() {
        let lines = []
        for (let i = 0; i < this.row; i++) {
            if (this.lineIsFull(this.board[i])) {
                lines.push(i)
            }
        }
        
        if (lines.length > 0) {
            this.scoring = true
            this.lines = lines
            this.score()
        }
    }

    score() {
        this.scoring = true
        this.process = setInterval(() => {
            this.echoFullLines()
        }, 1000 / this.fullLinesEchoSpeed)
    }
    
    //  full lines  full lines
    echoFullLines() {
        // 
        if (this.game.isPaused()) {
            return
        }

        if (this.count++ > this.fullLinesEchoTimes * 2 - 1) {
            this.count = 0
            this.scoring = false
            clearInterval(this.process)
            this.clearFullLines()
            return
        }
        let color = this.count % 2 == 0 ? this.defaultColor 
                                        : this.indicateColor
        this.lines.map((li) => {
            this.setLine(li, color, false)
        })
    }

    //  echoFullLines()  full lines 
    // defaultColor 
    clearFullLines() {
        let ls = this.lines
        ls.forEach((li) => {
            let deleted = this.board.splice(li, 1)[0]
            this.board.unshift(deleted)
        })
        this.scoreFunc(ls.length)
        this.lines = null
    }
}


