import GameIndicator from '../game/game_indicator'
import { pauseState, unpauseState } from '../const'

export default class PauseSign extends GameIndicator {
    //    
    constructor() {
        super('#s_psign')
        this.process = null
        this.paused = false
        this.count = 0
    }

    static instance(...args) {
        this.i = this.i || new this(...args)
        return this.i
    }

    pause() {
        this.paused = true
        // 
        this.process = setInterval(() => {
            this.count++ % 2 == 0 ? this.setState(pauseState) :
                                    this.setState(unpauseState)
        }, 1000 / 4)
    }

    unpause() {
        this.paused = false
        this.count = 0
        this.setState(unpauseState)
        clearInterval(this.process)
    }
}


